.login {
    height: 100vh;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}

.login__container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login__container >h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

.login__container > form >h5 {
    margin-bottom: 5px;
}
.login__container > form >input {
    height: 30px;
    margin: 10px;
    background-color: white;
    width: 98%;
}
.login__container > p {
    margin-bottom: 5px;
    font-size: 12px;
}
.login__signInButton{
    background-color: #F0C14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #A88734 #9C7E31 #846A29;

}

.login__registerButton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;


}
